
.xd__tag {
  display: inline-block;
  line-height: 1.2;
  cursor: pointer;
  color: #1f2d3d;
  -webkit-appearance: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  white-space: nowrap;
  font-size: 1.4rem;
  margin: 0px;
  border-radius: 12px;
  padding: 3px 8px;
}
.xd__tag.is-round {
  border-radius: 200px;
}
.xd__tag--default {
  color: #515a6e;
  background: #fff;
  border: 1px solid #dcdee2;
}
.xd__tag--success {
  color: #fff;
  background: #19be6b;
  border: 1px solid #19be6b;
}
.xd__tag--warning {
  color: #fff;
  background: #ff9900;
  border: 1px solid;
}
.xd__tag--error {
  color: #fff;
  background: #ed3f14;
  border: 1px solid;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.wrap {
  margin: 10px;
}
h1,
h3 {
  margin: 0;
}
h3 {
  font-weight: normal;
  font-size: 16px;
}
h4 {
  font-weight: normal;
  font-size: 14px;
  text-align: left;
  background-color: #e9e9e9;
  padding: 10px;
}
h2 {
  text-align: left;
  width: 100%;
  border-bottom: 1px dashed #ededed;
  font-size: 16px;
  height: 40px;
  line-height: 40px;
  text-indent: 10px;
  margin-top: 30px;
  margin-bottom: 0;
}
.btn__group {
  border: 1px solid #eee;
  padding: 10px;
  position: relative;
  margin: 0 20px;
}
.btn__group::before {
  content: '\6309\94AE\7EC4';
  font-size: 12px;
  position: absolute;
  top: -6px;
  right: -20px;
  background: #fff;
  color: #aaa;
}
.btn__block {
  margin: 0 auto!important;
}
.wrap > .xd__btn,
.xd__tag {
  margin-bottom: 10px;
}
